@import '../common/styles/sass/variables';

.skillsBlock {
  background-color: $bgSecondColor;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.skillsContainer {
  justify-content: center;
  width: 70%;
  height: 70%;
}

.title {
  background-color: #d5c2c2;
}

.skills {
  border: 1px blue solid;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.test {
  border: purple 2px solid;
  display: flex;
  flex-wrap: wrap;
}

